import React, { useState } from "react";
import { MoreHorizontal } from "react-feather";

import Card from "../Card/Card";
import Dropdown from "../Dropdown/Dropdown";
import Editable from "../Editabled/Editable";

import "./Board.css";

function Board(props) {
	const [showDropdown, setShowDropdown] = useState(false);

	return (
		<div className="board">
			<div className="board_header">
				<p className="board_header_title">
					{props.board?.title}
					<span>{props.board?.cards?.length || 0}</span>
				</p>
				<div className="board_header_title_more"
					onClick={(event) => {
						event.stopPropagation();
						setShowDropdown(true);
					}}
				>
					<MoreHorizontal />
					{showDropdown && (
						<Dropdown class="board_dropdown" onClose={() => setShowDropdown(false)}>
							<p onClick={() => props.removeBoard()}>Deletar Evento</p>
						</Dropdown>
					)}
				</div>
			</div>
			<div className="board_cards custom-scroll">
				{props.board?.cards?.map((item) => (
					<Card
						key={item.id}
						card={item}
						boardId={props.board.id}
						removeCard={props.removeCard}
						updateCard={props.updateCard}
					/>
				))}
				<Editable
					text="+ Adicione uma atividade"
					placeholder="Insira o nome da atividade"
					displayClass="board_add-card"
					editClass="board_add-card_edit"
					onSubmit={(value) => props.addCard(props.board?.id, value)}
				/>
			</div>
		</div>
	);
}

export default Board;
